'use client';

import { User } from '@/types/User';
import { useState } from 'react';

interface Props {
  user: User;
  onDelete: (id: number) => void;
  onUpdate: (id: number, data: Omit<User, 'id'>) => void;
}

export default function UserCard({ user, onDelete, onUpdate }: Props) {
  const [editing, setEditing] = useState(false);
  const [name, setName] = useState(user.name);
  const [email, setEmail] = useState(user.email);

  const handleUpdate = () => {
    if (name && email) {
      onUpdate(user.id, { name, email });
      setEditing(false);
    }
  };

  return (
    <div className="flex justify-between items-center p-4 bg-white shadow rounded mb-2">
      {editing ? (
        <div className="flex-1 mr-4">
          <input
            type="text"
            value={name}
            className="w-full mb-2 px-2 py-1 border rounded"
            onChange={(e) => setName(e.target.value)}
          />
          <input
            type="email"
            value={email}
            className="w-full px-2 py-1 border rounded"
            onChange={(e) => setEmail(e.target.value)}
          />
        </div>
      ) : (
        <div className="flex-1">
          <p className="font-semibold">{user.name}</p>
          <p className="text-sm text-gray-600">{user.email}</p>
        </div>
      )}

      <div className="flex gap-2">
        {editing ? (
          <>
            <button
              onClick={handleUpdate}
              className="text-green-600 hover:underline"
            >
              Save
            </button>
            <button
              onClick={() => setEditing(false)}
              className="text-gray-600 hover:underline"
            >
              Cancel
            </button>
          </>
        ) : (
          <>
            <button
              onClick={() => setEditing(true)}
              className="text-blue-600 hover:underline"
            >
              Edit
            </button>
            <button
              onClick={() => onDelete(user.id)}
              className="text-red-600 hover:underline"
            >
              Delete
            </button>
          </>
        )}
      </div>
    </div>
  );
}